/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { Button, Dashicon, PanelBody } from '@wordpress/components';
import { useSelect, useDispatch } from '@wordpress/data';
import { useState } from '@wordpress/element';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { noop } from 'lodash';
import { STORE_NAME as NAB_ACTIONS } from '@nab/conversion-actions';
import { isEmpty } from '@nab/utils';
import type { ConversionAction as CA, GoalId } from '@nab/types';

/**
 * Internal dependencies
 */
import './style.scss';
import { STORE_NAME as NAB_EDITOR } from '../../../store/config';

export type ConversionActionProps = {
	readonly readOnly?: boolean;
	readonly action: CA;
	readonly goalId: GoalId;
};

export const ConversionAction = ( {
	readOnly,
	action,
	goalId,
}: ConversionActionProps ): JSX.Element => {
	const experimentId = useExperimentId();
	const goal = useActiveGoal();
	const goalIndex = useActiveGoalIndex();
	const actionType = useConversionActionType( action.type );

	const View = actionType?.view;
	const Edit = actionType?.edit;
	const Icon = actionType?.icon;
	const attributes = action?.attributes || {};
	const validate = actionType?.validate ?? ( () => ( {} ) );

	const errors = validate( attributes );
	const [ initialOpen ] = useState( ! isEmpty( errors ) );

	const setAttributes = useSetAttributes( goalId, action.id );
	const remove = useRemove( goalId, action.id );

	if ( readOnly ) {
		return (
			<div className="nab-conversion-action">
				<div className="nab-conversion-action__view">
					{ !! Icon ? (
						<Icon className="nab-conversion-action__icon" />
					) : (
						<Dashicon
							className="nab-conversion-action__icon nab-conversion-action__icon--invalid"
							icon="warning"
						/>
					) }
					<div className="nab-conversion-action__actual-view">
						{ !! View ? (
							<View
								attributes={ attributes }
								experimentId={ experimentId }
								goal={ goal.attributes }
								goalId={ goalId }
								goalIndex={ goalIndex }
							/>
						) : (
							<span>
								{ _x(
									'Invalid conversion action.',
									'text',
									'nelio-ab-testing'
								) }
							</span>
						) }
					</div>
				</div>
			</div>
		);
	} //end if

	return (
		<PanelBody
			className="nab-conversion-action"
			initialOpen={ initialOpen }
			title={
				( (
					<div className="nab-conversion-action__view">
						{ !! Icon ? (
							<Icon className="nab-conversion-action__icon" />
						) : (
							<Dashicon
								className="nab-conversion-action__icon nab-conversion-action__icon--invalid"
								icon="warning"
							/>
						) }
						<div className="nab-conversion-action__actual-view">
							{ !! View ? (
								<View
									attributes={ attributes }
									experimentId={ experimentId }
									goal={ goal.attributes }
									goalId={ goalId }
									goalIndex={ goalIndex }
								/>
							) : (
								<span>
									{ _x(
										'Invalid conversion action.',
										'text',
										'nelio-ab-testing'
									) }
								</span>
							) }
						</div>
					</div>
				 ) as unknown ) as string
			}
		>
			<div className="nab-conversion-action__edit">
				{ !! Edit ? (
					<Edit
						attributes={ attributes }
						experimentId={ experimentId }
						goal={ goal.attributes }
						goalId={ goalId }
						goalIndex={ goalIndex }
						setAttributes={ setAttributes }
						errors={ errors }
					/>
				) : (
					<span>
						{ _x(
							'This conversion action cant be properly loaded. Please consider removing it.',
							'text',
							'nelio-ab-testing'
						) }
					</span>
				) }
			</div>

			<Button
				isLink
				isDestructive
				onClick={ remove }
				className="nab-conversion-action__delete-button"
			>
				{ _x( 'Delete', 'command', 'neli-ab-testing' ) }
			</Button>
		</PanelBody>
	);
};

// =====
// HOOKS
// =====

const useExperimentId = () =>
	useSelect( ( select ) => select( NAB_EDITOR ).getExperimentId() );

const useActiveGoal = () =>
	useSelect( ( select ) => select( NAB_EDITOR ).getActiveGoal() );

const useActiveGoalIndex = () =>
	useSelect( ( select ) => select( NAB_EDITOR ).getActiveGoalIndex() );

const useConversionActionType = ( type ) =>
	useSelect( ( select ) =>
		select( NAB_ACTIONS ).getConversionActionType( type )
	);

const useSetAttributes = ( goalId, actionId ) => {
	const { updateConversionAction } = useDispatch( NAB_EDITOR );
	return goalId && actionId
		? ( attrs ) => updateConversionAction( goalId, actionId, attrs )
		: noop;
};

const useRemove = ( segmentId, ruleId ) => {
	const { removeConversionActionsFromGoal } = useDispatch( NAB_EDITOR );
	return segmentId && ruleId
		? () => removeConversionActionsFromGoal( segmentId, ruleId )
		: noop;
};
