/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { CheckboxControl, SelectControl } from '@wordpress/components';
import { useDispatch, useSelect } from '@wordpress/data';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA } from '@nab/data';
import type { Goal as RealGoal, WooCommerceOrderStatusName } from '@nab/types';
type Goal = Omit< RealGoal, 'conversionActions' >;

/**
 * Internal dependencies
 */
import './style.scss';
import { STORE_NAME as NAB_EDITOR } from '../../../store/config';

export const WooCommerceGoalSettings = (): JSX.Element | null => {
	const isWooCommerceGoal = useIsWooCommerceGoal();
	const [ checked, onToggle ] = useOrderRevenueToggle();
	const statuses = useWooCommerceOrderStatuses();
	const [ orderStatus, setOrderStatus ] = useOrderStatus();

	if ( ! isWooCommerceGoal ) {
		return null;
	} //end if

	return (
		<div className="nab-woocommerce-goal-settings">
			<p>
				<strong>
					{ _x(
						'WooCommerce Goal Settings',
						'text',
						'nelio-ab-testing'
					) }
				</strong>
			</p>
			<SelectControl
				label={ _x(
					'Order status required for conversions:',
					'command',
					'nelio-ab-testing'
				) }
				options={ statuses }
				value={ orderStatus }
				onChange={ setOrderStatus }
			/>
			<CheckboxControl
				label={ _x(
					'Track revenue and show in test results',
					'command',
					'nelio-ab-testing'
				) }
				checked={ checked }
				onChange={ onToggle }
			/>
		</div>
	);
};

// =====
// HOOKS
// =====

const useIsWooCommerceGoal = () =>
	useSelect( ( select ) => {
		const { getActiveGoal } = select( NAB_EDITOR );
		const { getConversionActions } = select( NAB_EDITOR );
		const goalId = getActiveGoal()?.id;
		const actions = getConversionActions( goalId );
		return (
			actions.length &&
			actions.every( ( { type } ) => 'nab/wc-order' === type )
		);
	} );

const useOrderRevenueToggle = () => {
	const goal = useGoal();
	const value = goal.attributes.useOrderRevenue ?? true;
	const { updateGoal } = useDispatch( NAB_EDITOR );
	const update = () => updateGoal( goal.id, { useOrderRevenue: ! value } );
	return [ value, update ] as const;
};

const useOrderStatus = () => {
	const goal = useGoal();
	const value = goal.attributes.orderStatusForConversion ?? 'wc-completed';
	const { updateGoal } = useDispatch( NAB_EDITOR );
	const update = ( v: WooCommerceOrderStatusName ) =>
		updateGoal( goal.id, { orderStatusForConversion: v } );
	return [ value, update ] as const;
};

const useWooCommerceOrderStatuses = () =>
	useSelect( ( select ) =>
		select( NAB_DATA ).getWooCommerceSetting( 'orderStatuses' )
	);

const useGoal = (): Goal =>
	useSelect( ( select ) => select( NAB_EDITOR ).getActiveGoal() );
