/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { useDispatch, useSelect } from '@wordpress/data';
import { useEffect } from '@wordpress/element';
import { _x } from '@wordpress/i18n';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_EXPERIMENTS } from '@nab/experiments';
import type { ConversionAction as CA, GoalId } from '@nab/types';

/**
 * Internal dependencies
 */
import './style.scss';
import { ConversionAction } from '../conversion-action';
import { STORE_NAME as NAB_EDITOR } from '../../../store/config';

export const AutomaticConversionActionList = (): JSX.Element => {
	const goalId = useActiveGoalId();
	const conversionActions = useConversionActions( goalId );

	useAutoConversionActionsEffect();

	return (
		<div className="nab-conversion-action-list nab-conversion-action-list--automatic">
			{ 1 === conversionActions.length ? (
				<p
					key="conversion-action-explanation"
					className="nab-conversion-action-list__help"
				>
					{ _x(
						'Split tests are usually run with some specific goals in mind. In this type of test, theres a new conversion when the following conversion action occurs after the visitor has seen the tested element:',
						'text',
						'nelio-ab-testing'
					) }
				</p>
			) : (
				<p
					key="conversion-action-explanation"
					className="nab-conversion-action-list__help"
				>
					{ _x(
						'Split tests are usually run with some specific goals in mind. In this type of test, theres a new conversion when at least one of the following conversion actions occurs after the visitor has seen the tested element:',
						'text',
						'nelio-ab-testing'
					) }
				</p>
			) }
			{ conversionActions.map( ( action ) => (
				<ConversionAction
					key={ action.id }
					goalId={ goalId }
					action={ action }
					readOnly={ true }
				/>
			) ) }
		</div>
	);
};

// =====
// HOOKS
// =====

const useActiveGoalId = () =>
	useSelect( ( select ) => select( NAB_EDITOR ).getActiveGoal()?.id );

const useConversionActions = ( goalId: GoalId ): ReadonlyArray< CA > =>
	useSelect( ( select ) => {
		if ( ! goalId ) {
			return [];
		} //end if
		const { getConversionActions } = select( NAB_EDITOR );
		return getConversionActions( goalId ) || [];
	} );

const useAutoConversionActionsEffect = () => {
	const { replaceConversionActionsInGoal } = useDispatch( NAB_EDITOR );

	const goalId = useActiveGoalId();
	const control = useSelect( ( select ) =>
		select( NAB_EDITOR ).getAlternative( 'control' )
	);
	const automaticGoalSetup = useSelect( ( select ) => {
		const { getExperimentType } = select( NAB_EDITOR );
		const { getExperimentSupport } = select( NAB_EXPERIMENTS );
		return getExperimentSupport(
			getExperimentType(),
			'automaticGoalSetup'
		);
	} );

	useEffect( () => {
		if ( ! goalId || ! automaticGoalSetup ) {
			return;
		} //end if

		const auto = automaticGoalSetup( control.attributes );
		replaceConversionActionsInGoal( goalId, auto );
	}, [ automaticGoalSetup, goalId, control ] );
};
